<template>
  <div>
    <!-- 자식 컨포넌트 -->
    <!-- HTML태그에 ref="id"를 지정하면 Vue컴포넌트의 함수에서 this.$refs를 통해 접근이 가능함 -->
    <button type="button" @click="childFunc" ref="btn">CLICK</button>
  </div>
</template>
<script>
export default {
  methods: {
    childFunc() {
      console.log("부모 컴포넌트에서 직접 발생시킨 이벤트");
    },
  },
};
</script>
<style></style>
